.trending {
    width: 70%;
    margin: 1vw auto;
    background: white;
    border-radius: 1vw;
    padding: 1vw 2vw;

    .trends {
        width: 100%;
        margin: 0 auto;

        h2 {
            font-size: 1vw;
        }

        ul {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 0;
            list-style: none;

            li {
                width: 18%;
                position: relative;

                .icon {
                    position: absolute;
                    right: 1vw;
                    top: 1vw;
                    color: #ddd;
                    cursor: pointer;

                    &.red {
                        color: red;
                    }
                }

                .img {
                    width: 100%;
                    height: 12.5vw;
                    padding: 1vw;

                    img {
                        cursor: pointer;
                        width: 100%;
                        height: 100%;
                        aspect-ratio: 1;
                        object-fit: contain;
                    }
                }

                h2 {
                    font-size: 1vw;
                    height: 6vw;
                }
            }
        }
    }
}

