.searchComp {
    width: 60%;
    padding: 0 1vw 0 2vw;
    margin: 0 auto 2vw auto;
    border-radius: 4vw;
    height: 4vw;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;

    input {
        width: 100%;
        background: none;
        border: none;
        height: 100%;
        border-top-left-radius: 3vw;
        border-bottom-left-radius: 3vw;
        outline: none;
    }

    button {
        font-size: 2vw;
        color: #444;
        border: none;
        background: none;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 3vw;
        aspect-ratio: 1;
        cursor: pointer;
    }
}